import React, { useEffect,useRef ,useState,useCallback} from 'react'
import Label from '../Label/Label';
import './menu.less'
import ReactTooltip from 'react-tooltip';
import Icon from '../Icon/Icon';
import Content from '../Content/Content';
import Tooltip from '../Tooltip/Tooltip';

export default function Menu({ lang, closeSideMenu }) {
  const moreMenuRef = useRef(null);
  const docsMenuRef = useRef(null)
  const menus = [
    { id: 'pools', text: lang['pools'], link: 'https://app.deri.finance/#/pool' },
    { id: 'Futures', text: lang['Futures'], link: 'https://app.deri.finance/#/trade/futures' },
    { id: 'options', text: lang['options'], link: 'https://app.deri.finance/#/trade/options', icon: <Label text={lang['beta']} height='16' borderRadius='10' bgColor='#CD7A37' borderColor='#CD7A37' color='#FFFFFF' fontSize='11' className='beta-icon' /> },
    { id: 'token', text: lang['token'], link: 'https://app.deri.finance/#/token' },
    { id: 'bridge', text: lang['bridge'], link: 'https://bridge.deri.finance/#/bridge' },
    {
      id: 'docs', text: lang['docs'],ref: docsMenuRef, subMenu: [
        { id: 'white-paper', text: lang['white-paper'], link: 'https://docs.deri.finance/whitepaper' },
        { id: 'code-audit', text: lang['code-audit'], link: 'https://docs.deri.finance/code-audits' },
        { id: 'team', text: lang['team'], link: 'https://deri.finance/#/team' },
        { id: 'faq', text: lang['faq'], link: 'https://docs.deri.finance/general-faq' },
        { id: 'guide', text: lang['guide'], link: 'https://docs.deri.finance/' },
        { id: 'github', text: lang['github'], link: 'https://github.com/deri-finance/' }
      ]
    },
    {
      id: 'more', text: lang['more'], ref: moreMenuRef, subMenu: [
        { id: 'governance', text: lang['governance'], link: 'https://governance.deri.finance/#/governance' },
        { id: 'stats', text: lang['stats'], link: 'https://info.deri.finance/#/info' },
        { id: 'airdrop', text: lang['airdrop'], link: 'https://app.deri.finance/#/airdrop' },
        { id: 'promotional-event', text: lang['promotional-event'], link: 'https://app.deri.finance/#/trade-to-earn' },
        { id: 'retired-pool', text: lang['retired-pool'], link: 'https://v2app.deri.finance/#/retired' }
      ]
    },
    // { id: 'trade-to-earn', text: lang['trade-to-earn'], icon: <Icon width='16' height='20' token = 'activity' />, link: 'https://app.deri.finance/#/trade-to-earn', className: 'long' }
  ]
 

  return (
    <div className='menu'>
      <div className='back-icon' onClick={() => closeSideMenu(false)}>
        <Icon token='back' width='16' height='16' />
      </div>
      {menus.map(menu => {
        return (
          <span className={`menu-item ${menu.id}-item`} key={menu.id}>
            {menu.subMenu 
              ? 
                <SubMenu menu={menu} closeSideMenu={closeSideMenu}/>
              :
                <>
                  {menu.icon}
                  <a href={menu.link} onClick={closeSideMenu}>{menu.text}</a>
                </>
            }
          </span>

        )
      })}
      <Content />
    </div>
  )
}

function SubMenu({menu,closeSideMenu}){
  const [isTooltipOpen, setTooltipOpen] = useState(false)
  const tooltipWrapperRef = useRef(null)

  const calcPosition = (position,currentEvent,currentTarget,refNode) => {
    const rect = currentTarget.getBoundingClientRect();
    const top = rect.top + rect.height + 24;
    return { top: top, left: rect.left }
  }

  const hideTooltipHandler = useCallback(() => {
    const tooltipWrapperEl = tooltipWrapperRef.current
    if (tooltipWrapperEl) {
      ReactTooltip.hide(tooltipWrapperEl)
      setTooltipOpen(false)
      document.removeEventListener("click", hideTooltipHandler)
    }
  }, [])

  const onTooltipTriggerClick = useCallback(() => {
    const tooltipWrapperEl = tooltipWrapperRef.current
    if (tooltipWrapperEl) {
      if (isTooltipOpen) {
        ReactTooltip.hide(tooltipWrapperEl)
        setTooltipOpen(false)
      } else {
        ReactTooltip.show(tooltipWrapperEl)
        setTooltipOpen(true)
        setTimeout(() => {
          document.addEventListener("click", hideTooltipHandler)
        },0)
      }
    }
  }, [isTooltipOpen])

  useEffect(() => {
    return () => {
      document.removeEventListener("click", hideTooltipHandler)
    }
  }, [])
  return (
    <>
      <div className='sub-item' 
        data-tip 
        data-for={menu.id} 
        ref={tooltipWrapperRef}
        data-event="click"
        data-event-off= ''
        onClick={onTooltipTriggerClick}>
        {menu.text}
        <Icon token={isTooltipOpen ? 'arrow-up' : 'arrow-down'} width='20' height='20'/>
      </div>
      <Tooltip id={menu.id}
        overridePosition={calcPosition}
        scrollHide={false}
        isCapture
        clickable >
        {
          menu.subMenu.map(subMenu => <div className={`sub-menu-item ${subMenu.id}-item`} key={subMenu.id}><a  href={subMenu.link} onClick={closeSideMenu} rel='noreferrer' alt=''>{subMenu.text}</a></div>)
        }
      </Tooltip>
      {menu.icon}
    </>
  )
}